@import url(card.css);

img {
  width: 40vw;
  height: auto;
}

h2,
h3 {
  font-size: 1.5rem;
  margin-bottom: 2vh;
}

h4,
h5 {
  font-size: 1rem;
  margin-top: 1vh;
  margin-bottom: 1vh;
}

.sections {
  margin-bottom: 5%;
}

div#sommaire {
  width: fit-content;
}
#sommaire h2 {
  margin-bottom: 5%;
}

ol {
  color: orangered;
  list-style-type: none;
}

ol li {
  margin-bottom: 2px;
  transition: 0.5s;
}

a {
  text-decoration: none;
  color: orangered;
  transition: 0.5s;
}

ol li:hover {
  color: rgb(131, 128, 128);
}
a:hover {
  color: rgb(131, 128, 128);
}

#Presentation div {
  margin-bottom: 2%;
}

.quick-links {
  display: block;
  width: max-content;
}

.text-justify {
  text-align: justify;
}

.blockWebGl {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 0;
}

.blockWebGl > div {
  flex-basis: 35vw;
}

.noeud-container {
  display: flex;
  align-items: center;
  justify-content: center;
}

#noeud-object {
  flex-basis: 30vw;
  outline: none;
  mix-blend-mode: exclusion;
}

.blockCode {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.blockCode > div {
  flex-basis: 35vw;
}

#canvas-codepen {
  flex-basis: 35vw;
}

/** Mobile */
@media (max-width: 480px) {
  .blockWebGl > div {
    flex-basis: 100%;
  }
  .blockCode > div {
    flex-basis: 100%;
  }
  #canvas-codepen {
    flex-basis: 100%;
  }
}

/** Tablet */
@media (max-width: 768px) {
  .blockWebGl > div {
    flex-basis: 100%;
  }
  .blockCode > div {
    flex-basis: 100%;
  }
  #canvas-codepen {
    flex-basis: 100%;
  }
}

#prosCons {
  display: flex;
  justify-content: space-around;
}

#vertical-line {
  margin-left: 1%;
  margin-right: 1%;
  border-right: 2px solid orangered;
}

#pros,
#cons {
  width: 40%;
  text-align: center;
}
#prerequis > * {
  margin: 15px;
}
#prerequis h5 {
  font-size: 1.25rem;
}
#prerequis {
  background-color: #1818183f;
  width: 100%;
}
#prerequis ul {
  margin-left: 5%;
  width: 90%;
  text-align: justify;
}
#prerequis ul li {
  margin-bottom: 10px;
}

.highlight {
  background-color: #1818183f;
  font-weight: bolder;
}
.blockOfCode {
  background-color: #333333;
  font-family: "Roboto Mono", monospace;
  overflow: auto;
  white-space: pre-wrap;
  box-sizing: border-box;
  padding: 2%;
  margin: 1%;
  width: 700px;
}

.blockOfCode ul {
  list-style-type: none;
}

li.linedeleted {
  background-color: #412e32;
}

li.lineadded {
  list-style-type: none;
  background-color: #293832;
}

.kwd {
  color: #b294bb;
}
.pln {
  color: #c5c8c6;
}
.pun {
  color: #c5c8c6;
}
.str {
  color: #b5bd68;
}
.typ {
  color: #81a2be;
}
.lit {
  color: #de935f;
}
.com {
  color: #969896;
}

footer {
  background-color: transparent;
  margin-bottom: 20px;
  height: fit-content;
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer-btn {
  color: orangered;
  font-weight: bold;
  border-radius: 5px;
  border: 3px solid orangered;
  padding: 8px 14px;
}
.footer-btn:hover{
  color: white;
  background-color: orangered;
  transition: 0.1s linear;
}