@font-face {
  font-family: "FatFace";
  src: url(../static/fonts/AbrilFatface-Regular.ttf);
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Firefox < 16 */
@-moz-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Internet Explorer */
@-ms-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Opera < 12.1 */
@-o-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

* {
  margin: 0;
  padding: 0;

  -webkit-animation: fadein 1.5s; /* Safari, Chrome and Opera > 12.1 */
  -moz-animation: fadein 1.5s; /* Firefox < 16 */
  -ms-animation: fadein 1.5s; /* Internet Explorer */
  -o-animation: fadein 1.5s; /* Opera < 12.1 */
  animation: fadein 1.5s;
}

@import url(icons.css);
@import url(contents.css);

html {
  scroll-behavior: smooth;
}

html,
body {
  height: 100vh;
  font-family: "Poppins";
}

body, .main-container {
  overflow-x: hidden;
}

.canvas-container {
  background-color: rgb(24, 24, 24);
}

.webgl {
  position: absolute;
  top: 0;
  left: 0;
  outline: none;
  mix-blend-mode: exclusion;
}

.container {
  height: 100vh;
  display: grid;
  place-items: center;
}

h1 {
  font-size: 5rem;
  text-transform: uppercase;
  color: white;
  font-family: "Poppins";
}

section {
  width: 75vw;
  margin-top: 5vh;
  margin-left: 14vw;
}

.scroll-down {
  height: 50px;
  width: 30px;
  border: 2px solid white;
  border-radius: 50px;
  position: absolute;
  left: 50%;
  bottom: 20px;
  cursor: pointer;
}

.scroll-down::before,
.scroll-down::after {
  content: "";
  position: absolute;
  top: 20%;
  left: 50%;
  height: 10px;
  width: 10px;
  border: 2px solid white;
  border-top: transparent;
  border-left: transparent;
  transform: translate(-50%, -100%) rotate(45deg);
  animation: scroll-down 1s ease-in-out infinite;
}

.scroll-down::after {
  top: 30%;
  animation-delay: 0.3s;
}

@keyframes scroll-down{
  0%{
    opacity: 0;
  }
  30%{
    opacity: 1;
  }
  60%{
    opacity: 1;
  }
  100%{
    opacity: 0;
    top: 90%;
  }
}

@media (max-width: 800px) {
  h1 {
    font-size: 3rem;
  }
}
@media (max-width: 500px) {
  h1 {
    font-size: 3rem;
    text-align: center;
  }
}